.product-week__wrapper {
  position: relative;

  display: flex;
  flex-direction: column;

  width: @mobile-width;
  margin: 0 auto;
  padding: 28px 30px 44px;

  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;

    width: 100%;
    height: 7px;

    background-image: url("../img/bg-zigzag-line.svg");
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: -8px 0;
  }
}

.product-week__title {
  display: flex;
  flex-direction: column;
}

.product-week span {
  margin-top: 5px;
  .font(17px, 20px, 700, @seafoam-blue);
  text-transform: uppercase;
}

.product-week__content {
  position: relative;

  max-width: 292px;
  padding-top: 23px;
}

.product-week__description {
  margin: 0 0 17px 0;
}

.product-week__details {
  width: 100%;
  margin: 0 0 28px 0;

  line-height: 20px;

  border-collapse: collapse;
}

.product-week__image-wrapper {
  width: 260px;
}

.product-week__feature,
.product-week__value {
  padding: 10px 28px 10px 0;
}

.product-week__details tr {
  border-bottom: 1px solid @light-gray;
}

.product-week__price {
  margin: 18px 0 25px 0;

  font-size: 17px;
  font-weight: 700;
  text-align: center;
}

@media (min-width: @tablet-width) {
  .product-week__wrapper {
    width: @tablet-width;
    padding: 41px 46px 70px;

    &::before {
      height: 17px;
      background-position: -19px 0;
    }
  }

  .product-week__title {
    flex-direction: row;
  }

  .product-week span {
    display: block;

    width: 70px;
    margin: 12px 0 0 145px;

    text-align: center;
    line-height: 17px;
  }

  .product-week__content {
    padding-top: 50px;
    padding-left: 3px;
  }

  .product-week__image-wrapper {
    position: absolute;
    top: 57px;
    right: -379px;

    width: 333px;
  }

  .product-week__description {
    margin-bottom: 36px;
  }

  .product-week__details {
    margin-bottom: 56px;

    font-size: 15px;
    line-height: 21px;
  }

  .product-week__feature,
  .product-week__value {
    padding: 9px 0 10px 0;
  }

  .product-week__price {
    margin-bottom: 34px;

    font-size: 20px;
    line-height: 24px;
  }
}

@media (min-width: @desktop-width) {
  .product-week__wrapper {
    width: @desktop-width;
    padding: 123px 50px 95px 97px;

    &::before {
      left: 49px;

      width: 1052px;
      height: 21px;

      background-position: -28px 0;
    }
  }

  .product-week__title {
    justify-content: start;
  }

  .product-week span {
    order: -1;
    margin: 14px 52px 0 0;
  }

  .product-week__content {
    margin-left: 124px;
    padding-top: 49px;
  }

  .product-week__image-wrapper {
    top: -109px;
    left: 353px;
    z-index: -1;

    width: 525px;
  }

  .product-week__description {
    margin-bottom: 50px;
  }

  .product-week__details {
    margin-bottom: 109px;
  }

  .product-week__button {
    min-width: 292px;
  }
}
